<template>
    <component
        :is="skillComponent"
        :agent="agent"
        :skillList="skillList"
        :disabledSkillList="disabledSkillList"
        @favorite="favoriteHandle"
        @prompt-click="$emit('prompt-click', $event)"
        @close="$emit('close', $event)"
    ></component>
</template>
<script setup lang="ts">
import { ref, computed, watch, defineProps } from 'vue';
import { showMobile } from '@/utils/util';
import SkillMap from './SkillMap.vue';
import SkillMapMobile from './SkillMapMobile.vue';
import { getAssistantSkills } from '@/api/skill';
import { AgentType, doFavorite } from '@/api/market';

const loading = ref(true);
const isMobile = showMobile();
const props = defineProps<{ agent: AgentType }>();
const skillComponent = computed(() => {
    return isMobile ? SkillMapMobile : SkillMap;
});
const skillList = ref<{ id: string; name: string; icon: any }[]>([]);
const disabledSkillList = ref<{ id: string; name: string; icon: any }[]>([]);
const favoriteHandle = (isLiked: boolean) => {
    doFavorite(props.agent?.id, isLiked);
};
watch(
    () => props.agent,
    () => {
        if (props.agent) {
            loading.value = true;
            getAssistantSkills(props.agent.agentCode)
                .then((response) => {
                    skillList.value = (response?.data?.ownedList || []).map(
                        (item: any) => {
                            return {
                                ...item,
                            };
                        },
                    );
                    disabledSkillList.value = (
                        response?.data?.unownedList || []
                    ).map((item: any) => {
                        return {
                            ...item,
                        };
                    });
                })
                .finally(() => (loading.value = false));
        }
    },
    { immediate: true },
);

defineExpose({ favoriteHandle });
</script>
