<script setup lang="ts">
import '@hwork/icon/star';
import {
    ref,
    defineProps,
    watch,
    computed,
    inject,
    reactive,
    nextTick,
    onMounted,
} from 'vue';
import { AgentType, editAssistant } from '@/api/market';
import Avatar from '@/components/Market/pc/CreateAssistant/Avatar.vue';
import { Modal, FormInstance, message } from '@hwork/ant-design-vue';
import emptyDataImage from '@/assets/images/empty-data.png';
import skillIcon from '@/assets/icons/icon-meeting.svg';
import favoriteIcon from '@/assets/icons/favorite.svg';
import favoriteIconFill from '@/assets/icons/favorite-fill.svg';
import promptIcon from '@/assets/icons/icon-prompt-text.svg';
import SkillAvarat from './SkillAvarat.vue';
import { deleteAssistant } from '@/api/market';
import { queryChatGroupList } from '@/hooks/useChatGroupList';
type ListType = { id: string; name: string; icon: any; children: any[] }[];
const props = defineProps<{
    skillList: ListType;
    disabledSkillList: ListType;
    agent: AgentType;
}>();
const emit = defineEmits(['favorite', 'prompt-click', 'close']);
const updateAgent = inject<(target: any) => void>('updateAgent', () => {});
const skillMapRef = ref<HTMLDivElement>();

const setAgentStatus = inject<(target: any) => void>(
    'setAgentStatus',
    () => {},
);
const setMessage = inject<(target: string) => void>('setMessage', () => {});
const sendPrompt = inject<(target: string) => void>('send-prompt', () => {});

const updateLoading = ref<boolean>(false);
const pageType = ref<'detail' | 'edit'>('detail');
const avatarRef = ref<any>();
const formRef = ref<FormInstance>();
const formState = reactive<Record<string, string>>({
    name: '',
    description: '',
    prompts: '',
});
const rules = {
    name: [{ required: true, message: '请输入助手名称' }],
    description: [{ required: true, message: '请输入助手简介' }],
    prompts: [{ required: true, message: '请输入助手设定' }],
};
const isLiked = ref(false);
const toggleLike = () => {
    isLiked.value = !isLiked.value;
    emit('favorite', isLiked.value);
    updateAgent({ userFavorites: isLiked.value });
};

const getWidthNumber = (item: string) => {
    const wrapper = document.getElementById('hwork-assistant-app');
    if (wrapper) {
        const textElement = document.createElement('span');
        textElement.innerText = item;
        textElement.style.fontSize = `14px`;
        wrapper.appendChild(textElement);
        const width = textElement.getBoundingClientRect().width;

        textElement.parentNode?.removeChild(textElement);
        return width + 38;
    }

    return 0;
};
const getWidth = (item: string) => {
    return `${getWidthNumber(item)}px`;
};
const editHandle = () => {
    pageType.value = 'edit';
};

const deleteHandle = () => {
    Modal.confirm({
        zIndex: 1001,
        closable: false,
        content: '助手删除后无法使用该助手，且删除后不可恢复，是否确认删除？',
        onOk: () => {
            deleteAssistant(props.agent.agentCode).then(() => {
                emit('close');
                setAgentStatus('deleted');
            });
        },
    });
};

const cancelHandle = () => {
    formState.name = props.agent?.agentName;
    formState.description = props.agent?.agentDesc;
    formState.prompts = props.agent?.agentPrompts || '';
    pageType.value = 'detail';
};
const submitHandle = () => {
    formRef.value!.validateFields().then((values) => {
        const avatarData = avatarRef.value?.getAvatarData();
        updateLoading.value = true;
        editAssistant({
            ...values,
            ...avatarData,
            code: props.agent.agentCode,
        })
            .then((res) => {
                if (res.code === 10000) {
                    message.success('更新成功');
                    pageType.value = 'detail';
                    updateAgent({
                        agentDesc: values.description,
                        agentIcon: avatarData.icon,
                        iconBackground: avatarData.iconBackground,
                        agentName: values.name,
                        agentPrompts: values.prompts,
                    });
                    queryChatGroupList();
                } else {
                    message.error(res.msg);
                }
            })
            .finally(() => {
                updateLoading.value = false;
            });
    });
};

const clickPrompt = (item: string) => {
    setMessage(item);
    emit('close');
};

const chatRunning = inject('chatRunning');
const sendMessage = (item: string) => {
    if (chatRunning.value) {
        message.info('正在生成中，请先停止回答');
        return;
    }
    sendPrompt(item);
    emit('close');
};
const prompts = computed(() => {
    return props.agent.prompts || [];
});

watch(
    () => props.agent,
    () => {
        if (props.agent) {
            isLiked.value = !!props.agent.userFavorites;

            formState.name = props.agent.agentName;
            formState.description = props.agent.agentDesc;
            formState.prompts = props.agent?.agentPrompts || '';
        }
    },
    { immediate: true },
);
</script>
