import request from '@/utils/request';

// 获取领域列表
export function getSkillList(params: Record<string, any>) {
    return request<Res<{ items: Record<string, any>[]; total: number }>>({
        url: '/api/agent-base/v1/skills',
        params,
    });
}
// 获取助手当前技能
export function getAssistantSkills(agentCode: string) {
    return request<Res<{ ownedList: Record<string, any>[]; unownedList: Record<string, any>[] }>>({
        url: '/api/agent-manager/v1/agent/market/skills',
        params: { agentCode },
    });
}
