            />
        </div>
        <div v-if="agent && agent.agentType !== 'personal'" style="width: 100%">
            <div v-if="skillList.length" class="skill-card-menu">
                <div class="skill-card-menu-title">已拥有技能</div>
                <div class="skill-card-menu-list">
