<script setup lang="ts">
import '@hwork/icon/close';
import '@hwork/icon/left';
import '@hwork/icon/heart';
import '@hwork/icon/heart-fill';
import '@hwork/icon/edit';

import promptIcon from '@/assets/icons/icon-prompt-text.svg';
import { ref, defineProps, computed, watch, inject } from 'vue';
import { AgentType, deleteAssistant } from '@/api/market';
import { showConfirmDialog } from 'vant';
import emptyDataImage from '@/assets/images/empty-data.png';
import skillIcon from '@/assets/icons/icon-meeting.svg';
import ActionSheet from 'vant/es/action-sheet';
import { isiOS } from '@/utils/util';
import SkillAvarat from './SkillAvarat.vue';
import { useRouter, useRoute } from 'vue-router';
import qs from 'query-string';
import 'vant/es/action-sheet/style';
import 'vant/es/dialog/style';

type ListType = { id: string; name: string; icon: any }[];
const props = defineProps<{
    skillList: ListType;
    disabledSkillList: ListType;
    agent: AgentType;
}>();

const router = useRouter();
const route = useRoute();
const emit = defineEmits(['favorite', 'prompt-click', 'close']);
const isLiked = ref(false);
const open = ref(false);
const children = ref<any[]>([]);

const updateAgent = inject<(target: any) => void>('updateAgent', () => {});
const setAgentStatus = inject<(target: any) => void>(
    'setAgentStatus',
    () => {},
);
const toggleLike = () => {
    isLiked.value = !isLiked.value;
    emit('favorite', isLiked.value);
};

const closeHandle = () => {
    emit('close');
};
const clickPrompt = (target: string, send = false) => {
    const redirect = route.query?.redirect as string;
    if (redirect) {
        const to = decodeURIComponent(redirect);
        const path = to.split('?')?.[0];
        const params = qs.parse(to.split('?')?.[1]);
        if (path) {
            router.push({
                path,
                query: {
                    ...params,
                    viewPrompt: encodeURIComponent(target),
                    sendPrompt: send ? 'true' : 'false',
                },
            });
        }
    } else {
        router.go(-1);
    }
};

const wrapperHeight = computed(() => {
    //if (!isiOS()) {
    return 'calc(100vh - 44px)';
    //}
    //return 'calc(100vh - 100px)';
});

const prompts = computed(() => {
    return props.agent.prompts || [];
});
const clickSkill = (target: any) => {
    if (props.agent.agentCode === 'super') {
        children.value = target;
        open.value = true;
    }
};

const editHandle = () => {
    const redirect = route.query?.redirect as string;
    router.push({
        path: '/create/assistant',
        query: {
            agentCode: props.agent.agentCode,
            back: 'skill',
            redirect,
        },
    });
};

const deleteHandle = () => {
    showConfirmDialog({
        title: '删除助手',
        message: '助手删除后无法使用该助手，且删除后不可恢复，是否确认删除？',
    })
        .then(() => {
            deleteAssistant(props.agent.agentCode).then(() => {
                //setAgentStatus('deleted');
                router.push({
                    path: '/chat/new',
                    query: {
                        agent: 'super',
                    },
                });
            });
        })
        .catch(() => {});
};

watch(
    () => props.agent,
    () => {
        if (props.agent) {
            isLiked.value = !!props.agent.userFavorites;
        }
    },
    { immediate: true },
);
</script>

